<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="用户ID">
            <el-input v-model.number="requestParam.uid"></el-input>
          </el-form-item>
          <el-form-item label="刷选类别">
            <template>
              <el-select
                v-model="requestParam.search_item"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </template>
          </el-form-item>
          <el-form-item label="金额范围">
            <el-input
              v-model.number="requestParam.diff_range[0]"
              placeholder="最低金额"
              size="small"
              class="minInput"
            ></el-input>
            -
            <el-input
              v-model.number="requestParam.diff_range[1]"
              placeholder="最高金额"
              size="small"
              class="minInput"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getList">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="uid" label="用户ID" align="center">
          </el-table-column>
          <el-table-column prop="nick_name" label="昵称" align="center">
          </el-table-column>
          <el-table-column prop="code" label="注册账户类型" align="center">
            <template slot-scope="scope">
              <span>{{codes[scope.row.code]}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="user_name" label="注册账号" align="center">
            <template slot-scope="scope">
              <span>{{ scope.row.user_name | hideAccount }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="total_gold" label="总金币" align="center">
          </el-table-column>
          <el-table-column prop="box_gold" label="保险箱金币" align="center">
          </el-table-column>
          <el-table-column prop="recharge" label="总充值" align="center">
          </el-table-column>
          <el-table-column prop="give" label="总赠出" align="center">
          </el-table-column>
          <el-table-column prop="receive" label="总收到" align="center">
          </el-table-column>
          <el-table-column prop="winLose" label="总输赢" align="center">
          </el-table-column>
          <el-table-column prop="tax" label="游戏税收" align="center">
          </el-table-column>
          <el-table-column prop="reward" label="奖励" align="center">
          </el-table-column>
          <el-table-column prop="trade_tax" label="交易税" align="center">
          </el-table-column>
          <el-table-column prop="flow" label="总流水" align="center">
          </el-table-column>
          <el-table-column prop="uc" label="后台调整" align="center">
            <!-- <template slot-scope="scope">
              <el-collapse accordion>
                <el-collapse-item>
                  <template slot="title">
                    uc
                  </template>
                  <div>
                    <label>achieveTarget:</label
                    ><span>{{ scope.row.uc.achieveTarget }} </span>
                  </div>
                </el-collapse-item>
              </el-collapse>
            </template> -->
          </el-table-column>
        </el-table>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        uid: null,
        diff_range: [],
        search_item: null
      },
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      options: [
        {
          value: "total_gold",
          label: "总金币"
        },
        {
          value: "winLose",
          label: "输赢"
        },
        {
          value: "flow",
          label: "流水"
        },
        {
          value: "reward",
          label: "奖励"
        },
        {
          value: "give",
          label: "赠与"
        },
        {
          value: "receive",
          label: "收到"
        }
      ],
      codes: {
        0: "app游客",
        1: "手机号",
        2: "微信",
        3: "QQ",
        4: "Google",
        5: "Facebook"
      }
    };
  },
  methods: {
    getList() {
      if (
        this.requestParam.diff_range[0] != undefined &&
        this.requestParam.diff_range[1] != undefined
      ) {
        if (
          this.requestParam.diff_range[0] >= this.requestParam.diff_range[1]
        ) {
          this.$message({
            showClose: true,
            message: "金额上限必须小于下限!",
            type: "error"
          });
          return;
        }
      }
      var that = this;
      that.page.list = [];
      that.page.total =0;
      var list = [];
      var total = 0;
      let query = {
        uid: this.requestParam.uid,
        diff_range: this.requestParam.diff_range,
        search_item: this.requestParam.search_item,
        page: {
          current_page: this.page.current_page,
          page_size: this.page.page_size
        }
      };
      this.$api.statistic.list_playerAccount(query).then(res => {
        if (res.data.code === 200) {
          // that.page.total = res.data.data.total;
          // that.page.list = res.data.data.list;
          total = res.data.data.total;
          list = res.data.data.list;
          let uids = [];
          list.forEach(item => {
            uids.push(item.uid);
          });
          this.$api.statistic.getUidList({ uuids: uids }).then(res => {
            if (res.data.success === true) {
              let userinfos = res.data.data.bwb_infos;
              list.map((item, index) => {
                item.nick_name = userinfos[item.uid].nick_name;
                item.code = userinfos[item.uid].code;
                item.user_name = userinfos[item.uid].user_name;
                return item;
              });
              that.page.list = list;
              that.page.total = total;
            }
          });
        } else {
          this.$message({
            showClose: true,
            message: "获取列表失败",
            type: "error"
          });
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.getList();
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.getList();
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.getList();
      }
    },
    addColor(diff) {
      if (diff => 0) {
        return "greenCor";
      } else {
        return "redCor";
      }
    }
  },
  filters: {
    formatDate: function(value) {
      return moment(value).format("YYYY/MM/DD hh:mm:ss a");
    },
    hideAccount: function(account) {
      if (!account || [...account].length < 5) {
        return "-";
      }
      let arryStr = [...account];
      let str = "";
      let start = Math.floor(arryStr.length / 2) - 1;
      let end = Math.floor(arryStr.length / 3) - 1 + start;
      if (end - start > 4) {
        end = start + 4;
      }
      arryStr.forEach((item, index) => {
        if (index >= start && index <= end) {
          item = "*";
        }
        str += item;
      });
      return str;
    }
  }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
.greenCor {
  font-size: larger;
  color: green;
}
.redCor {
  color: red;
}
</style>



